import React, { Component } from "react";
import Youtube from "./Youtube";
class Home extends Component {
  render() {
    return (
      <div>
        <h2>Welcome to Wesleyan Kendo</h2>
        <h3>Practice Schedule</h3>
        <p>
          We currently practice Wednesday Night at 7pm in the Jones Room
          at the Center for the Arts. 
        </p>
        <p>Here you can find resources about our club and Kendo in general.
          <br></br><br></br>
          Please contact Hyunseok Choi: 
          <br></br>
          <b>hchoi03@wesleyan.edu</b>
          <br></br>
          for any questions.
        </p>
        <Youtube></Youtube>
 
      </div>
    );
  }
}
 
export default Home;


